// Ionic Variables and Theming. For more info, please see:
// http://ionicframework.com/docs/theming/

/** Ionic CSS Variables **/
:root {
  --ion-color-primary: #c85c67;
  --ion-color-primary-rgb: 200, 92, 103;
  --ion-color-primary-contrast: #ffffff;
  --ion-color-primary-contrast-rgb: 255, 255, 255;
  --ion-color-primary-shade: #b0515b;
  --ion-color-primary-tint: #ce6c76;

  --ion-color-secondary: #dbbe52;
  --ion-color-secondary-rgb: 219, 190, 82;
  --ion-color-secondary-contrast: #000000;
  --ion-color-secondary-contrast-rgb: 0, 0, 0;
  --ion-color-secondary-shade: #c1a748;
  --ion-color-secondary-tint: #dfc563;

  --ion-color-tertiary: #599ae0;
  --ion-color-tertiary-rgb: 89, 154, 224;
  --ion-color-tertiary-contrast: #000000;
  --ion-color-tertiary-contrast-rgb: 0, 0, 0;
  --ion-color-tertiary-shade: #4e88c5;
  --ion-color-tertiary-tint: #6aa4e3;

  --ion-color-success: #9bd9b4;
  --ion-color-success-rgb: 155, 217, 180;
  --ion-color-success-contrast: #000000;
  --ion-color-success-contrast-rgb: 0, 0, 0;
  --ion-color-success-shade: #88bf9e;
  --ion-color-success-tint: #a5ddbc;

  --ion-color-warning: #ffe95c;
  --ion-color-warning-rgb: 255, 233, 92;
  --ion-color-warning-contrast: #000000;
  --ion-color-warning-contrast-rgb: 0, 0, 0;
  --ion-color-warning-shade: #e0cd51;
  --ion-color-warning-tint: #ffeb6c;

  --ion-color-danger: #ff8f9e;
  --ion-color-danger-rgb: 255, 143, 158;
  --ion-color-danger-contrast: #000000;
  --ion-color-danger-contrast-rgb: 0, 0, 0;
  --ion-color-danger-shade: #e07e8b;
  --ion-color-danger-tint: #ff9aa8;

  --ion-color-medium: #546980;
  --ion-color-medium-rgb: 84, 105, 128;
  --ion-color-medium-contrast: #ffffff;
  --ion-color-medium-contrast-rgb: 255, 255, 255;
  --ion-color-medium-shade: #4a5c71;
  --ion-color-medium-tint: #65788d;

  --ion-color-light: #f4f5f8;
  --ion-color-light-rgb: 244, 245, 248;
  --ion-color-light-contrast: #000000;
  --ion-color-light-contrast-rgb: 0, 0, 0;
  --ion-color-light-shade: #d7d8da;
  --ion-color-light-tint: #f5f6f9;

}

